const Anomalyforcing = (props) => {
  const {
    buttonLabel,
    className
  } = props;


  const [modal, setModal] = useState(false);
  const [unmountOnClose] = useState(true);

  const toggle = () => setModal(!modal);
  // const handleClick = () => {
  //   console.log(props.url)
  // };

  return (
    <div>
      <Form inline onSubmit={(e) => e.preventDefault()}>


        <Button color="danger" onClick={toggle}>{buttonLabel}<span className="fa fa-refresh"></span></Button>
      </Form>
      <Modal aria-labelledby="contained-modal-title-vcenter"
        centered isOpen={modal} toggle={toggle} className={className} unmountOnClose={unmountOnClose} >
        <ModalHeader toggle={toggle}>   <div style={{ color: "black", fontWeight: 'bold', textAlign: "center" }}>  <span >  Replanification Colis </span> </div> </ModalHeader>
        <ModalBody size="lg">


          <Settentative colis={props.url} changet={()=>{setModal(false);props.getdata()}}  />



        </ModalBody>
        <ModalFooter>

          


        </ModalFooter>
      </Modal>
    </div>
  );
}


export default Anomalyforcing;