import React , {useState,useEffect} from "react";
import axios from 'axios'
import {Table } from "react-bootstrap"
import Aux from "../../../hoc/_Aux";
import moment from "moment"
var urlbackend = require("../../../env/env")();


function TableAnomalie(props){
  const [anomalies , setanomalies] = useState([])
  const [colisid] = useState(props.colisse)



    useEffect(()=>{
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios(urlbackend + "/anomaly/delivery/colis/"+colisid,config).then((response)=>{

            console.log(response.data?.data?.data)
            setanomalies(response.data.data.data)
        }).catch(()=>{
            console.log("e")
        });

    })


    return(

<Aux>
<Table striped bordered hover size="sm">
  <thead>
    <tr>
      <th>Date retour</th>
      <th>Message Livreur</th>
      <th>Note</th>
      
    </tr>
  </thead>
  <tbody>

    {anomalies.map(e=>(
 <tr key={e.id}>
 <td>{ moment(e.created_at).format('DD-MM-YYYY, HH:mm')} </td>
 <td>{e.anomaly?.message}</td>
 <td>{e.note}</td>
 
</tr>

    ))}
  {/*   <tr>
      <td>1</td>
      <td>Mark</td>
      <td>Otto</td>
      <td>@mdo</td>
    </tr>
    <tr>
      <td>2</td>
      <td>Jacob</td>
      <td>Thornton</td>
      <td>@fat</td>
    </tr>
    <tr>
      <td>3</td>
      <td colSpan="2">Larry the Bird</td>
      <td>@twitter</td>
    </tr> */}
  </tbody>
</Table>


</Aux>

    )

}
export default TableAnomalie