import React from "react";

//import { PageTitle } from '../../layout-components';
import { IconButton, Box, Card, CardContent } from "@material-ui/core";
import { InputGroup, FormControl, Button } from "react-bootstrap";
import axios from "axios";
import { Row, Col, Tabs, Tab } from "react-bootstrap";
import QRcode from '../../QRcode/QRcode'
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
import Aux from "../../../hoc/_Aux";
import Motifretour from '../../Admin/Anomaly/Show.anomalies'
import Anomalyforcing from './Annulation/Annulation'
var urlbackend = require("../../../env/env")();

class RetourManagmenet extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            pendingPackages: [],
            ongoingPackages: [],
            enlevePackages: [],

            drivers: [],
            driver: "",
        };
    }
    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        /*    axios.get(
              urlbackend + "/package/pending?current_page=1&per_page=500",
              config
          ).then((response)=>{
              this.setState({ pendingPackages: response.data.data.data });
              console.log(response.data)
          })
          axios.get(urlbackend + "/driver", config).then((response)=>{
              this.setState({ drivers: response.data.data });
  
          }) */
        var getpendingPackages = axios.get(
            urlbackend + "/delivery/reschedule/list?current_page=1&per_page=5000",
            config
        )

        var getdrivers = axios.get(urlbackend + "/driver", config);

        axios
            .all([getpendingPackages, getdrivers])
            .then(
                axios.spread((...response) => {
                    this.setState({ pendingPackages: response[0].data.data.data });
                    this.setState({ drivers: response[1].data.data });
                    console.log(response[0].data.data.data)
                    /*     this.setState({ pendingPackages: response[0].data.data.data });
                        this.setState({ ongoingPackages: response[1].data.content });
                        this.setState({ enlevePackages: response[2].data.content });
    
                        this.setState({ drivers: response[3].data.content }); */
                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });

    }
    savedriver(iddriver, idpacket) {
        //

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.put(
            urlbackend + "/delivery/to_reschedule",
            { driver: iddriver, colis: [idpacket] },
            config
        )
            .then((response) => {
                alert("add Succefull");
                console.log("response.data", response.data);
                this.componentDidMount();
                //this.reloaddata()
                this.setState({ drivers: [] });
            })
            .catch((e) => {
                console.log(e);
            });
    }
    reloaddata() {
        this.componentDidMount()
    }

    changecolor(valeur) {
        switch (valeur) {
            case 0:
                return "#eeeeca"
            case 2:
                return "#ffe7f1"
            case 1:
                return "#bedcff"
            case 3:
                return "#dcadb1"
            default:
                break;
        }

    }

    annulercolis(id) {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        confirmAlert({
            title: ' ',
            message: 'Confirmer Annulation de colis?',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {

                        axios.put(
                            urlbackend + "/dispatch/colis/to_retour_final/" + id, config)
                            .then((response) => {
                                alert("add Succefull");
                                console.log("response.data", response.data);
                                this.componentDidMount();
                                //this.reloaddata()
                                this.setState({ drivers: [] });
                            })
                            .catch((e) => {
                                console.log(e);
                            });
                    }

                },
                {
                    label: 'No',
                    onClick: null
                }
            ]
        });
    }
    render() {
        return (
            <Aux>

                <Row>
                    <Col>
                        <button className="btn btn-info" onClick={() => this.reloaddata()} >   Actualiser</button>


                        <hr />
                        <Tabs defaultActiveKey="home">
                            <Tab eventKey="home" title="">
                                <Card className="card-box mb-4">
                                    <div className="card-header">
                                        <div className="card-header--title">
                                            {/* <b>This table card has custom content</b> */}
                                        </div>
                                        <Box className="card-header--actions">
                                            <IconButton
                                                size="small"
                                                color="primary"
                                                className="text-primary"
                                                title="View details"
                                            ></IconButton>
                                        </Box>
                                    </div>
                                    <CardContent className="p-0">
                                        <div className="table-responsive">
                                            <table className="table  table-hover text-nowrap mb-0">
                                                <thead className="table thead-light mb-0">
                                                    <tr>
                                                        <th>Numéro colis</th>
                                                        <th>Fournisseur</th>
                                                        <th className="text-center">Client</th>
                                                        <th className="text-center">addresse de client</th>
                                                        <th className="text-center">Chauffeur</th>
                                                        <th className="text-center">Date Retour</th>
                                                        <th className="text-center">Actions</th>
                                                        <th className="text-center"></th>
                                                        <th className="text-center"></th>
                                                        <th className="text-center"></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {this.state.pendingPackages.map((pickup, index) => (
                                                        <tr key={index} style={{ background: this.changecolor(pickup.tentatif) }}>
                                                            <td className="text-center center">
                                                                <div className="font-weight-bold text-black"
                                                                    title="...">
                                                                    {pickup.colis.index_colis}

                                                                </div>
                                                            </td>
                                                            <td>
                                                                <div className="d-flex align-items-center">
                                                                    <a
                                                                        href="#/"
                                                                        onClick={(e) => e.preventDefault()}
                                                                        className="font-weight-bold text-black"
                                                                        title="..."
                                                                    >


                                                                        {pickup.check_replanification === 1 ? <i className="fa fa-circle text-c-red f-10 m-r-15" /> : <i className="fa fa-circle text-c-green f-10 m-r-15" />}
                                                                        {pickup.colis.provider.username} :{" "}
                                                                        {pickup.colis.provider.phone_number}
                                                                    </a>

                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.name_complete} : {pickup.colis.phone_number}
                                                                    <span >

                                                                    </span>
                                                                </div>
                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.city} :{" "}
                                                                    {pickup.colis.address}
                                                                </div>
                                                            </td>
                                                            <td className="text-center">

                                                                {pickup.driver
                                                                    ? pickup.driver.username
                                                                    : <div style={{ background: "#e863da" }}> "not approved" </div>}

                                                            </td>
                                                            <td className="text-center">
                                                                <div className="h-auto ">
                                                                    {pickup.colis.created_at}
                                                                </div>
                                                            </td>

                                                            <td>
                                                                <InputGroup className="mb-3">
                                                                    <FormControl
                                                                        as="select"
                                                                        id="{this.state.driver}"
                                                                        onChange={(value) =>
                                                                            this.savedriver(
                                                                                value.target.value,
                                                                                pickup.colis.id
                                                                            )
                                                                        }
                                                                        custom
                                                                    >
                                                                        <option>Sélectionner un chauffeur</option>

                                                                        {this.state.drivers.map((driver, index) => (
                                                                            <option
                                                                                className=" badge badge-info"
                                                                                key={driver.id}
                                                                                value={driver.id}
                                                                            >
                                                                                {driver.username}
                                                                            </option>
                                                                        ))}
                                                                    </FormControl>
                                                                </InputGroup>
                                                            </td>
                                                            <td>

                                                                <QRcode url={pickup.colis.id} > </QRcode>
                                                            </td>
                                                            <td>
                                                                {/* <div className="h-auto  badge badge-danger">
                                                                    <button className="badge-info" onClick={() => this.savedriver()}>savedriver</button>

                                                                </div>
                                                                <div className=" badge badge-danger">
                                                                    delete

                                                                </div> */}


                                                                <Anomalyforcing url={pickup} getdata={() => this.componentDidMount()} />
                                                            </td>
                                                            <td className="text-center">
                                                                <Box>
                                                                    <IconButton color="primary" size="small">
                                                                        <Motifretour colis={pickup.colis.id} />
                                                                    </IconButton>
                                                                </Box>
                                                            </td>
                                                        </tr>
                                                    ))}
                                                </tbody>
                                            </table>
                                        </div>
                                    </CardContent>
                                </Card>
                            </Tab>
                        </Tabs>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default RetourManagmenet;
